import { independent, locationOrigin } from '../tools/env'
export default name => {
    switch (name) {
        case 'login':
            if (independent) {
                if (process.env.NODE_ENV === 'development') {
                    window.location.href = '//' + window.location.host + locationOrigin + '/static/login.html?_=' + Math.random()
                } else {
                    window.location.href = '//' + window.location.host + '/static/login.html?_=' + Math.random()
                }
            } else {
                window.location.href = '//' + window.location.host + '/login.html?_=' + Math.random()
            }
            return true

        case 'outer_main':
            window.location.href = '//' + window.location.host + '/#/main'
            return true
        case 'outer_knowledgeBaseSummary':
            window.location.href = '//' + window.location.host + '/#/knowledgeBaseSummary' // 运维知识
            return true
        case 'outer_dataShareSummary':
            window.location.href = '//' + window.location.host + '/#/dataShareSummary' // 共享数据
            return true
        case 'outer_dataReportSummary':
            window.location.href = '//' + window.location.host + '/#/dataReportSummary' // 数据报表
            return true
        case 'outer_maintenancePersonSummary':
            window.location.href = '//' + window.location.host + '/#/maintenancePersonSummary' // 运维人员
            return true
        case 'outer_productionManageSummary':
            window.location.href = '//' + window.location.host + '/#/productionManageSummary' // 生产管理
            return true
        case 'outer_efficientOperationSummary':
            window.location.href = '//' + window.location.host + '/#/efficientOperationSummary' // 高效运维
            return true
        case 'outer_monitoringSummary':
            window.location.href = '//' + window.location.host + '/#/monitoringSummary' // 电站监视
            return true
        case 'outer_analyzeSummary':
            window.location.href = '//' + window.location.host + '/#/analyzeSummary' // 运行分析
            return true
        case 'outer_actionPlanSummary':
            window.location.href = '//' + window.location.host + '/#/actionPlanSummary' // 行动方案
            return true
        case 'outer_videoSurveillance':
            window.location.href = '//' + window.location.host + '/#/videoSurveillance' // 视频监控
            return true
        case 'home':
            window.location.href = '//' + window.location.host + '/#/home' // 数据查询
            return true
        case 'outer_expertAnalysis':
            window.location.href = '//' + window.location.host + '/#/expertAnalysis' // 专家分析
            return true
        case 'outer_dataExport':
            window.location.href = '//' + window.location.host + '/#/dataExport' // 数据导出
            return true
        case 'outer_workingConfigure':
            window.location.href = '//' + window.location.host + '/#/workingConfigure' // 运行配置
            return true
        case 'outer_settlementAmount':
            window.location.href = '//' + window.location.host + '/#/settlementAmount' // 结算电量
            return true
        case 'outer_Monitoring':
            window.location.href = '//' + window.location.host + '/#/Monitoring' // 首页一
            return true
        case 'outer_Resources':
            window.location.href = '//' + window.location.host + '/#/Resources' // 首页二
            return true
        case 'outer_Equipment':
            window.location.href = '//' + window.location.host + '/#/Equipment' // 首页三
            return true
        case 'outer_People':
            window.location.href = '//' + window.location.host + '/#/People' // 首页四
            return true
        case 'outer_monitoringSummaryWp':
            window.location.href = '//' + window.location.host + '/#/monitoringSummaryWp' // 电站监视---风电
            return true
        case 'outer_analyzeSummaryWp':
            window.location.href = '//' + window.location.host + '/#/analyzeSummaryWp' // 运行分析---风电
            return true
        case 'outer_actionPlanSummaryWp':
            window.location.href = '//' + window.location.host + '/#/actionPlanSummaryWp' // 行动方案---风电
            return true
        case 'outer_expertAnalysisWp':
            window.location.href = '//' + window.location.host + '/#/expertAnalysisWp' // 专家分析---风电
            return true
        default:
            return false
    }
}



