import axios from 'axios'
import qs from 'qs'
import router from '@sparePart/router/index'
import { Notification } from 'element-ui'

// 创建axios实例
const service = axios.create({
    timeout: 30000 // 请求超时时间
})

let CIP = ''
getLocalIPs(ips => {
    CIP = ips.join()
})

service.interceptors.request.use(config => config, error => Promise.reject(error))

service.interceptors.response.use(
    response => {
        if (response.status !== 200) console.error(response.status)
        if (response.headers.login_status === 'TIMEOUT') router.push({ name: 'login' })
        return response.data
    },
    error => {
        if (error.response && error.response.status === 404) {
            Notification.error({
                title: '错误',
                message: '接口不存在'
            })
        } else {
            Notification.error({
                title: '错误',
                message: error.response.status + ': 错误：接口请求错误'
            })
        }
        return Promise.reject(error.response)
    }
)

export function get (url, params = {}) {
    params.t = new Date().getTime()
    return service({
        url: url,
        method: 'get',
        headers: {
            'X-Requested-With': 'XMLHttpRequest',
            'CIP': CIP
        },
        params
    })
}

export function postJson (url, data = {}) {
    return service({
        url: url,
        method: 'post',
        headers: {
            'Content-Type': 'application/json;charset=UTF-8',
            'X-Requested-With': 'XMLHttpRequest',
            'CIP': CIP
        },
        data: JSON.stringify(data)
    })
}

export function postForm (url, data = {}) {
    return service({
        url: url,
        method: 'post',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
            'X-Requested-With': 'XMLHttpRequest',
            'CIP': CIP
        },
        data: qs.stringify(data)
    })
}

export function upload (url, data = {}) {
    const _data = new FormData()
    for (const key in data) {
        if (data.hasOwnProperty(key)) _data.append(key, data[key])
    }
    return service({
        url: url,
        method: 'post',
        headers: {
            'Content-Type': 'multipart/form-data',
            'X-Requested-With': 'XMLHttpRequest',
            'CIP': CIP
        },
        data: _data
    })
}

export const download = ({
    url,
    filename,
    callback = () => { },
    blob = false,
    download = true,
    progress = () => { }
}) => {
    const oReq = new XMLHttpRequest()
    oReq.open('GET', url, true)
    oReq.responseType = 'blob'
    oReq.addEventListener('progress', ({ total, loaded }) => progress(total, loaded), false)
    oReq.onload = function (oEvent) {
        if (oReq.response.size === 0 || oReq.status === 404) {
            if (confirm(' 文件为空或不存在，确定继续执行吗？')) __fun()
            return callback()
        } else __fun()

        function __fun () {
            const _blob = new Blob([oReq.response])
            if (download) {
                const elink = document.createElement('a')
                const hasFileName = oReq.getResponseHeader('content-disposition')
                elink.download = filename || (hasFileName && hasFileName.split(`UTF-8''`)[1] && decodeURI(hasFileName.split(`UTF-8''`)[1])) || '未命名.你想要的扩展名'
                elink.style.display = 'none'
                elink.href = URL.createObjectURL(_blob)
                elink.target = '_blank'
                document.body.appendChild(elink)
                elink.click()
                document.body.removeChild(elink)
                if (!blob) callback()
            }
            if (blob) {
                callback(_blob)
            }
        }
    }
    oReq.send()
}

export {
    service
}

export function getLocalIPs (callback) {
    var ips = []
    var RTCPeerConnection = window.RTCPeerConnection || window.webkitRTCPeerConnection || window.mozRTCPeerConnection

    var pc = new RTCPeerConnection({ iceServers: [] })
    pc.createDataChannel('')

    pc.onicecandidate = function (e) {
        if (!e.candidate) {
            pc.close()
            callback(ips)
            return
        }
        var ip = /^candidate:.+ (\S+) \d+ typ/.exec(e.candidate.candidate)[1]
        if (ips.indexOf(ip) === -1) ips.push(ip)
    }
    pc.createOffer(function (sdp) {
        pc.setLocalDescription(sdp)
    }, function onerror () { })
}



