// 是否独立部署
const pathname = window.location.pathname
export const independent = (pathname === '/') || (pathname === '/login.html')

// 是否生产环境
export const isProduction = window.location.host.includes('necloud.solway.cn')

// 根路径
export const locationOrigin = process.env.NODE_ENV === 'development' ? '/SparePartsManagement' : window.location.origin + window.location.pathname



